import { _getAllFee } from "@/services/setting/fee";
import {
  ProCard,
  ProDescriptions,
  ProForm,
  ProFormText,
} from "@ant-design/pro-components";
import { useRequest } from "@umijs/max";
import React, { useState } from "react";

export default function Index() {
  /**********************************狀態管理**********************************/
  const [stripeFee, setStripeFee] = useState<{
    percentage: number;
    fixed_dollar: number;
    billing_percentage: number;
  }>();
  const [vendPopupsFee, setVendPopupsFee] =
    useState<API.DefaultObjectResponse>();

  const { loading, error } = useRequest(_getAllFee, {
    onSuccess: (res) => {
      console.log("error", error);
      console.log("res", res);
      setStripeFee(res.data.stripe);
      setVendPopupsFee(res.data.vendpopups);
    },
  });

  /**********************************狀態管理**********************************/
  /**********************************組件初始化**********************************/
  /**********************************組件初始化**********************************/
  /**********************************異步函數**********************************/
  /**********************************異步函數**********************************/

  return (
    <ProCard title="Fee setting" className="min-h-[90vh]">
      <ProCard title="Stripe Fee" colSpan="50%">
        {loading ? (
          <div>Loading...</div> // 显示加载状态
        ) : error ? (
          <div>Error loading data</div> // 显示错误信息
        ) : (
          <ProDescriptions dataSource={stripeFee}>
            <ProDescriptions.Item label="Fee Percentage">
              {stripeFee?.percentage}
            </ProDescriptions.Item>
            <ProDescriptions.Item label="Fee Fixed dollar">
              {stripeFee?.fixed_dollar}
            </ProDescriptions.Item>
            <ProDescriptions.Item label="Billing Percentage">
              {stripeFee?.billing_percentage}
            </ProDescriptions.Item>
          </ProDescriptions>
        )}
      </ProCard>
      <ProCard title="Vend Popups Fee">
        <div style={{ height: 360 }}>右侧内容</div>
      </ProCard>
    </ProCard>
  );
}
